import { createRouter, createWebHistory } from 'vue-router';
import HomeView from '@/views/HomeView.vue';
import Login from '@/views/Login.vue';

const routes = [
    {
        path: '/',
        name: 'home',
        component: HomeView
    },
    {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '@/views/AboutView.vue')
    },
    {
        path: '/login',
        name: 'Login',
        component: Login
    }
    // ,
    // {
    //     path: '/kakaologin',
    //     name: 'KakaoLogin',
    //     component: () => import(/* webpackChunkName: "kakaologin" */ '@/components/KaKaoLogin.vue')
    // },
    // {
    //     path: '/naverlogin',
    //     name: 'NaverLogin',
    //     component: () => import(/* webpackChunkName: "naverlogin" */ '@/components/NaverLogin.vue')
    // },
    // {
    //     path: '/googlelogin',
    //     name: 'GoogleLogin',
    //     component: () => import(/* webpackChunkName: "googlelogin" */ '@/components/GoogleLogin.vue')
    // }
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
});

export default router;
